<script>
export default {
  name: 'NotFound',
  mounted() {
    this.$anime({
      targets: '.front-network path',
      keyframes: [
        {
          strokeDashoffset: [this.$anime.setDashoffset, 0],
          easing: 'easeInExpo',
          duration: 2000,
        },
        {
          opacity: 0,
          delay: 200,
          easing: 'easeInOutSine',
          duration: 1000,
        },
      ],
      loop: true,
    });

    this.$anime({
      targets: '.back-network path',
      strokeDashoffset: [this.$anime.setDashoffset, 0],
      easing: 'linear',
      direction: 'alternate',
      duration: 20000,
      loop: true,
    });

    this.$anime({
      targets: '.network__not_found_neuron',
      fill: ['#f00', '#fff'],
      duration: 1000,
      easing: 'easeInOutSine',
      loop: true,
    });
  },
};
</script>

<style lang="sass">
@import '../scss/smart-grid'
@import '../scss/variables'

.not_found_page
    margin: 0
    padding: 0
    width: 100vw
    height: 100vh
    display: grid
    background: linear-gradient(-125deg, $blue, black)
    display: grid
    align-items: center
    justify-items: center

    .back-network
      position: absolute
      top: -25vw
      height: 100vw
      width: 100vw
      filter: blur(4px)

    .front-network
      position: absolute
      top: -25vw
      height: 100vw
      width: 100vw

    .not_found_wrap
      place-self: center
      padding-bottom: 100px
      pointer-events: none
      z-index: 10

      @include to(60rem)
        width: 100vw
      @include from(60rem)
        width: 30vw

      svg
        color: #eff
        color: $blue
        opacity: .6

        @include to(60rem)
          font-size: 20vw
        @include from(60rem)
          font-size: 10vw


      h1, h2, h3
        font-family: Montserrat
        color: white

      h1
        font-size: 10vw
        font-weight: 800
        color: $blue
        opacity: .6

        @include to(60rem)
          font-size: 20vw
          line-height: 19vw
        @include from(60rem)
          font-size: 10vw
          line-height: 9.5vw

      h2
        font-size: 3vw
        font-weight: 800
        line-height: 5vw

        @include to(60rem)
          font-size: 6vw
          line-height: 10vw
        @include from(60rem)
          font-size: 3vw
          line-height: 5vw

      h3
        font-weight: 400
        opacity: .5
        font-size: 1vw

        @include to(60rem)
          font-size: 2vw
          letter-spacing: 0.08rem
        @include from(60rem)
          font-size: 1vw
          letter-spacing: 0.08rem
</style>
